.circle-list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle-list h2 {
    margin-bottom: 5px;
}

.circle-list-section {
    display: grid;
    grid-template-columns: repeat(5, 80px);
    grid-auto-rows: auto;
    gap: 10px 12px;
    padding: 10px 10px 10px 10px;
}

.circle-list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.circle-list-item img{
    border-radius: 50%;
    width: 80px;
    height: 80px;
    
    cursor: pointer;

    border: 4px solid transparent;
}

.circle-list-item img:hover{
    border: 4px solid grey;
}

.circle-list-item h4{
    margin-top: 0px;
    margin-bottom: 0px;
    width: 80px;

    text-align: center;
}

.circle-list-item-selected img {
    border: 4px solid rgb(32, 162, 197);
}

/*offset each even grid row*/
.circle-list-item:nth-child(12n - 6),
.circle-list-item:nth-child(12n - 5),
.circle-list-item:nth-child(12n - 4),
.circle-list-item:nth-child(12n - 3),
.circle-list-item:nth-child(12n - 2)
{
    margin-left: 86px;
}
