.navbar {
    position: absolute;
    margin-top: 2%;
    margin-left: 4%;
    width:80px;
    padding-top: 30px;
    padding-bottom: 35px;

    background-color: rgba(255, 255, 255, 65%);
    text-align: center;
}

.navbar-section {
    margin-bottom: 70px;
    display: grid;
    column-count: 1fr;
    grid-auto-rows: auto;
    row-gap: 12px;
}

.navbar-section:last-child {
    margin-bottom: 0px;
}

.navbar h3 {
    cursor: pointer;
    color: black;
    text-decoration-line: none;
    margin: 0px 0px 0px 0px;
     
}
/*NavLink returns <a>*/
.navbar a:link, .navbar a:visited, .navbar a:active {
    color: black;
    text-decoration-line: none;
}
.navbar h3:hover {
    text-decoration-line: underline;
}

.navbar img {
    width: 30px;
}

