.project-list {
    
}

.project-list h2 {
    text-align: center;
}

.project-list h4 {
    padding: 0;
    margin: 0;
}

.project-list-section {
    display: grid;
    grid-template-columns: repeat(3, 175px);
    grid-auto-rows: auto;
    row-gap: 10px;
    column-gap: 10px;
}

.project-list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid rgba(183, 208, 215, 40%);
    border-radius: 10%;
    padding: 10px 10px 10px 10px;

    outline: 2px solid black;
    outline-offset: -5px;

    background-color: #dae8e5;
    cursor: pointer;
}

.project-list-item img {
    width: 100px;
    height: 75px;
    margin-bottom: 5px;
}

.project-list-item-selected {
    outline: 4px solid rgb(32, 162, 197);
}