.project-panel {
    position: fixed;
    right: 3%;
    width: 26%;
    height: 100%;
    background-color: rgba(255, 255, 255, 65%);
    padding-left: 20px;
    padding-right: 20px;

}
.project-panel h3{
    margin-bottom: 8px;
}

.project-panel img {
    margin-bottom: 8px;
    width:100%;
    border: 2px solid black; 
}

.project-panel-exit {
    position: absolute;
    right: 4px;
    top: 4px;
    width: 20px;
    cursor: pointer;
}

.project-panel-tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.project-panel-tags div {
    padding: 3px 6px 3px 6px;
    margin-right: 10px;
    border-radius: 8%;
    background-color: #83a79d;
    color: white;
}